<template>
  <div class="UserForm">
    <van-form validate-first scroll-to-error @submit="onsubmit">
      <!-- 通过 pattern 进行正则校验 -->
      <van-field
        v-for="(item, index) in userinof"
        :key="item.name"
        v-model="item.value"
        :name="item.name"
        :placeholder="item.placeholder"
        :rules="item.rules"
        :readonly="item.readonly"
        clickable
        :maxlength="item.maxlength"
        :input-align="index < 3 ? 'right' : 'left'"
        @click="item.click ? emituserinof(item.click) : ''"
      >
        <div slot="label" class="input-text">{{item.label}}</div>
      </van-field>

      <slot></slot>

      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">提交</van-button>
      </div>

    </van-form>
  </div>
</template>

<script>
export default {
  name: "UserForm",
  props: {
    userinoflist: Array,
  },
  data() {
    return {
      userinof: [],
    };
  },
  components: {},
  methods: {
    // 返回userinof
    emituserinof(event) {
      this.$emit("updata", this.userinof);
      this.$emit(event);
    },
    // 点击提交表单
    onsubmit(e) {
      console.log(e);
    },
    oneway() {
      this.userinof = this.userinoflist.concat();
    },
  },
  created() {
    this.oneway();
  },
  watch: {
    userinoflist(newVal, oldVal) {
      this.oneway();
    },
  },
};
</script>

<style scoped>
.input-text {
  color: #333;
  font-weight: 600;
}
</style>