<template>
  <div class="binding">
    <!-- 右滑进入 -->
    <transition name="van-slide-right">
      <!-- 绑定会员卡 -->
      <van-popup :close-on-click-overlay="false" round :value="value">
        <div class="phone">
          <div class="title">绑定会员卡号</div>

          <!-- 输入区 -->
          <van-field
            v-model="binding"
            label="卡序号"
            required
            :error-message="bindingErr"
            placeholder="请输入卡序号"
          />

          <div class="button">
            <van-button native-type="button" plain block hairline type="default" @click="$emit('input', false)">取消</van-button>
            <van-button native-type="button" plain block hairline type="default" @click="emitbinding"><span class="determine">确定</span></van-button>
          </div>
        </div>
      </van-popup>
    </transition>
  </div>
</template>

<script>
export default {
  name: "binding",
  props: {
    value: {
      type: Boolean,
      default: false,
    },
		bindingErr: String
  },
  data() {
    return {
			binding: ''
    };
  },
  components: {},
  methods: {
		emitbinding(){
			this.binding.length > 0 && this.$emit('emitbinding', this.binding)
		},
	},
  computed: {
  },
};
</script>

<style scoped>
.phone {
  width: 80vw;
}
.title {
  color: #999;
  text-align: center;
  /* font-size: 12px; */
  line-height: 3em;
}

.button{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 10px;
}
.determine{
	color: #07c160;
}
</style>