<template>
  <div class="UpPhone">
    <!-- 右滑进入 -->
    <transition name="van-slide-right">
      <!-- 修改手机号码 -->
      <van-popup :close-on-click-overlay="false" round :value="value">
        <div class="phone">
          <div class="title">修改手机号码</div>
          <!-- 原手机号 -->

          <van-field :value="oldphone" readonly label="原手机号" />
          <!-- 新手机号 -->
          <van-field
            v-model="newphone"
            label="新手机号"
            required
            :error-message="inputBlur ? '' : Tips ? '不能与原手机号一致' : '手机号格式错误'"
            placeholder="请输入新手机号"
          />
          <!-- 短信验证码 -->
          <van-field v-model="testing" clearable :error-message="testingErr" required label="短信验证码" placeholder="请输入短信验证码">
            <template #button>
              <van-button size="small" type="primary" :disabled="down != 0" @click="downTime">{{down == 0 ? '发送验证码' : `再次发送(${down})`}}</van-button>
            </template>
          </van-field>

          <div class="button">
            <van-button native-type="button" plain block hairline type="default" @click="$emit('input', false)">取消</van-button>
            <van-button native-type="button" plain block hairline type="default" @click="determine"><span class="determine">确定</span></van-button>
          </div>
        </div>
      </van-popup>
    </transition>
  </div>
</template>

<script>
export default {
  name: "UpPhone",
  props: {
    value: {
      type: Boolean,
      default: false,
    },
		oldphone: String,
		testingErr: String
  },
  data() {
    return {
      newphone: "",
			testing: "",
			Tips: false,
			down: 0,
    };
  },
  components: {},
  methods: {
		determine(){
			this.inputBlur && this.testing.length > 0 && this.$emit('determine', this.newphone, this.testing)
		},
		// 倒计时
		downTime(){
			if(!this.inputBlur){
				return
			}
			this.down = 30
			let Time = setInterval( _=> {
				if(--this.down == 0){
					clearInterval(Time)
				}
			}, 1000)
		}
	},
  computed: {
    inputBlur() {
			let regex = /^1[34578]\d{9}$/;
			this.Tips = this.newphone == this.oldphone
			if(this.Tips){
				return false
			}else{
				return regex.test(this.newphone);
			}
      
		},
  },
};
</script>

<style scoped>
.phone {
  width: 80vw;
}
.title {
  color: #999;
  text-align: center;
  /* font-size: 12px; */
  line-height: 3em;
}

.button{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 10px;
}
.determine{
	color: #07c160;
}
</style>